import { GuessType } from "../types/guess";
import moment from 'moment';

export function scoreToEmoji(guesses: GuessType[]): string {

  const emojis = {
    incorrect: "🟥",
    correct: "🟩",
    skip: "⬛",
    empty: "⬜",
  };
  var now = moment(new Date());
  var end = moment("04-07-2022", "MM-DD-YYYY");
  var gameNo = Math.floor(moment.duration(now.diff(end)).asDays());
  var prefix = `Ainana Heardle #${gameNo + 1}\n\n`;
  // TODO: update todaysSolution to match this logic
  // to fix after kokoro harebare release so as to not mess current schedule

  let scoreEmoji = "";

  guesses.forEach((guess: GuessType) => {
    if (guess.isCorrect === true) {
      scoreEmoji += emojis.correct;
    } else if (guess.skipped === true) {
      scoreEmoji += emojis.skip;
    } else if (guess.isCorrect === false) {
      scoreEmoji += emojis.incorrect;
    } else {
      scoreEmoji += emojis.empty;
    }
  });

  if (guesses[0].isCorrect || guesses[1].isCorrect){
    prefix += `🔊`;
  }
  else if (guesses[2].isCorrect || guesses[3].isCorrect || guesses[4].isCorrect){
    prefix += `🔉`;
  }
  else {
    prefix += `🔇`;
  }

  let url = `\n\nhttps://aglovale.github.io/ainanaheardle/`;

  return `${prefix} ${scoreEmoji} ${url}`;
}
