import { IoMusicalNoteSharp } from "react-icons/io5";
import { Button } from "..";
import { loadStats } from "helpers/stats";
import * as Styled from "./index.styled";
import { Graph } from "../Graph";

interface Props {
  onClose: () => void;
}

export function StatsPopUp({ onClose }: Props) {
  const gameStats = loadStats();
  return (
    <Styled.Container>
      <Styled.PopUp>
        <Styled.Title><IoMusicalNoteSharp/> Statistics</Styled.Title>
        <Styled.Spacer />
        <Styled.Stats>
            <Styled.Text>
              Current Streak
              <span style={{fontSize:'28px'}}>{gameStats.currentStreak}</span>
            </Styled.Text>
            <Styled.Text>
              Best Streak
              <span style={{fontSize:'28px'}}>{gameStats.bestStreak}</span>
            </Styled.Text>
            <Styled.Text>
              Total Games
              <span style={{fontSize:'28px'}}>{gameStats.totalGames}</span>
            </Styled.Text>
            <Styled.Text>
              Win Rate
              <span style={{fontSize:'28px'}}>{gameStats.successRate}%</span>
            </Styled.Text>
          </Styled.Stats>
          
        <Graph gameStats={gameStats}/>
        <Button variant="green" style={{ marginTop: '15px' }} onClick={onClose}>
          Close
        </Button>
      </Styled.PopUp>
    </Styled.Container>
  );
}