/* eslint-disable */
import React, { useState } from "react";
import Countdown from 'react-countdown';
import { IoShareSocialSharp, IoStar } from "react-icons/io5";
import { IconContext } from "react-icons";

import { Song } from "../../types/song";
import { GuessType } from "../../types/guess";
import { scoreToEmoji } from "../../helpers";

import { Button } from "../Button";
import { YouTube } from "../YouTube";

import * as Styled from "./index.styled";

interface Props {
  didGuess: boolean;
  currentTry: number;
  todaysSolution: Song;
  guesses: GuessType[];
}

const tomorrowsDate = new Date();
tomorrowsDate.setSeconds(0);
tomorrowsDate.setMinutes(0);
tomorrowsDate.setHours(0);
tomorrowsDate.setDate(tomorrowsDate.getDate()+1);

const renderer = ({formatted: { hours, minutes, seconds }}: any) => {
    return <span>{hours}h {minutes}m {seconds}s</span>;
};

export function Result({didGuess, todaysSolution, guesses, currentTry}: Props) {
  const [toggle, setToggle] = useState(false);
  const showCopy = () => {
    setToggle(!toggle);
    setTimeout(() => setToggle(toggle), 3000);
  };

  if (didGuess) {
    const result = [];
    for (var i = 0; i < currentTry; i++){
      // LOGIC TO BE FIGURED OUT:
      // what should be colour difference
      // between unused attemps and skipped?
      // much to think about...
      if (i == currentTry-1){
        result.push(
        <IconContext.Provider value={{ style: { color:'#4DBB60' } }}>
          <IoStar color="4DBB60"/>
        </IconContext.Provider>
        );
        break;
      }
      result.push(
        <IconContext.Provider value={{ style: { color:'#BB4D4D' } }}>
          <IoStar color="BB4D4D"/>
        </IconContext.Provider>
      )
    }
    for (var i = currentTry; i < 5; i++){
      result.push(
        <IconContext.Provider value={{ style: { color:'#E6E6E6' } }}>
          <IoStar color="E6E6E6"/>
        </IconContext.Provider>
      )
    }

    const copyResult = React.useCallback(() => {
      navigator.clipboard.writeText(scoreToEmoji(guesses));
      showCopy();
    }, [guesses]);

    const triesConjugation = currentTry === 1 ? "second" : "seconds";
    const totalSeconds = ['1', '2', '4', '7', '16'];

    return (
      <>
        <Styled.ResultTitle>Congratulations!</Styled.ResultTitle>
        <YouTube id={todaysSolution.youtubeId} />
        The song today was <br /> 
        <Styled.SongTitle>
          {todaysSolution.name} -{" "}
          {todaysSolution.artist}
        </Styled.SongTitle>
        <Styled.Stars>{result}</Styled.Stars>
        <Styled.Tries>
          You got today's Ainana Heardle in {totalSeconds[currentTry-1]} {triesConjugation}.
        </Styled.Tries>
        <Button onClick={copyResult} variant="green">
          Share <IoShareSocialSharp size="20"/>
        </Button>
        {toggle && (<span>Copied to Clipboard!</span>)}
        <Styled.TimeToNext>
          Time to next song:
          <span style={{fontSize:'150%', display: 'block', fontWeight:'600'}}>
            <Countdown date={tomorrowsDate} renderer={renderer} zeroPadTime={2}/>
          </span>
        </Styled.TimeToNext>
      </>
    );
  } else {
    const result = [];
    for (var i = 0; i < 5; i++){
        result.push(
          <IconContext.Provider value={{ style: { color:'#BB4D4D' } }}>
            <IoStar color="BB4D4D"/>
          </IconContext.Provider>
        )
    }
    const copyResult = React.useCallback(() => {
      navigator.clipboard.writeText(scoreToEmoji(guesses));
      showCopy();
    }, [guesses]);
    return (
      <>
        <Styled.ResultTitle>Unfortunately...</Styled.ResultTitle>
        <YouTube id={todaysSolution.youtubeId} />
        The song today was <br /> 
        <Styled.SongTitle>
          {todaysSolution.name} -{" "}
          {todaysSolution.artist}
        </Styled.SongTitle>
        <Styled.Stars>{result}</Styled.Stars>
        <Button onClick={copyResult} variant="green">
          Share <IoShareSocialSharp size="20"/>
        </Button>
        {toggle && (<span>Copied to Clipboard!</span>)}
        <Styled.TimeToNext>
          Time to next song:
          <span style={{fontSize:'150%', display: 'block', fontWeight:'600'}}>
            <Countdown date={tomorrowsDate} renderer={renderer} zeroPadTime={2}/>
          </span>
        </Styled.TimeToNext>
      </>
    );
  }
}