import React from "react";
import { IoInformationCircleOutline, IoBarChartOutline } from "react-icons/io5";
import { IconContext } from "react-icons";
import CSS from 'csstype';

import * as Styled from "./index.styled";

interface Props {
  openInfoPopUp: () => void;
  openStats: () => void;
}

const statsButton: CSS.Properties = {
  justifySelf: 'end',
  width: '25px',
  height: '25px'
};

export function Header({ openInfoPopUp, openStats }: Props) {
  return (
    <Styled.Container>
      <Styled.Content>
        <IoInformationCircleOutline
          onClick={openInfoPopUp}
          size={30}
          width={30}
          height={30}
        />

        <Styled.Logo>Ainana Heardle</Styled.Logo>

          <IconContext.Provider value={{ style: statsButton }}>
            <IoBarChartOutline onClick={openStats}/>
          </IconContext.Provider>
      </Styled.Content>
    </Styled.Container>
  );
}
