import * as Styled from "./index.styled";
type Props = {
    index: number
    size: number
    label: string
}

export const Progress = ({ index, size, label }: Props) => {
    const time = ['1s', '2s', '4s', '7s', '16s'];
    return (
        <Styled.StatContainer>
            <Styled.StatAxis>{time[index]}</Styled.StatAxis>
            <Styled.Bar>
                <Styled.Label style={{ width: `${8 + size}%`}}>
                    {label}
                </Styled.Label>
            </Styled.Bar>
        </Styled.StatContainer>
    )
}